@keyframes slideInRight {
  0% {
    transform: translate3d(100%, 0, 0);
    visibility: visible; }

  100% {
    transform: translate3d(0, 0, 0); } }

.slideRight {
  animation-name: slideInRight;
  animation-duration: 1s; }

.landing-album-list .list-h2 {
  padding: 15px 0 25px 0; }
.landing-album-list .flipper {
  perspective: 1200px;
  width: 33%;
  display: inline-block; }
.landing-album-list figure {
  position: relative;
  transform-style: preserve-3d;
  transition: 1s transform;
  font-size: 1.6rem; }
  .landing-album-list figure img {
    width: 100%;
    height: auto; }
  .landing-album-list figure figcaption {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    transform: rotateY(0.5turn) translateZ(1px);
    background: rgba(255, 255, 255, 0.9);
    text-align: center;
    padding-top: 45%;
    opacity: 0.6;
    transition: 1s 0.5s opacity;
    color: black; }

.flipper:hover figure {
  transform: rotateY(0.5turn); }

.flipper:hover figure figcaption {
  opacity: 1; }

