.slider {
  height: 5px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 2px;
  position: relative;
  cursor: pointer; }
  .slider .fill {
    background: white;
    width: 36%;
    height: 5px;
    border-radius: 2px; }
  .slider .thumb {
    position: absolute;
    height: 10px;
    width: 10px;
    background: white;
    left: 36%;
    margin-left: -5px;
    top: 50%;
    margin-top: -5px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 100ms ease-in-out; }
  .slider:active .fill, .slider:active .thumb {
    transition: none; }
  .slider:hover .thumb, .slider:active .thumb {
    width: 20px;
    height: 20px;
    margin-top: -10px;
    margin-left: -10px; }

.play-pause, .next, .previous {
